html,
body {
	width: 100%;
	display: flex;
	flex: 1 1 auto;
}

html {
	min-height: 100%;
	scroll-behavior: smooth;
	overflow-x: hidden;
}

body {
	font-family: 'Gilroy';
	font-size: 14px;
	font-weight: 500;
	color: #494949;
	min-width: 320px;
	overflow-x: hidden;
}

.site-container {
	display: flex;
	flex-direction: column;
	flex: 1 1 auto;
	max-width: 1920px;
	width: 100%;
	margin: 0 auto;
}

.content {
	max-width: 1100px+100px;
	width: 100%;
	margin: 0 auto;
	padding: 0 50px;

	@include media(tablet) {
		padding: 0 15px;
	}
}

p {
	line-height: 1rem;
	margin: 0;
	color: #494949;
}


object svg {
	cursor: pointer;
}

h1 {
	font-weight: 500;
	color: #2868b2;
	font-size: 32px;
	line-height: 40px;
	margin: 0;
}

h2 {
	color: #2868b2;
	font-size: 26px;
	margin: 0;
	text-align: center;
}

h3 {
	color: #2868b2;
	font-size: 18px;
	margin: 0;
}

a {
	text-decoration: none;
	text-transform: none;
	color: inherit;
	cursor: pointer;
	transition: opacity .2s ease-out;

	&:hover {
		opacity: 0.8;
	}
}

svg {
	width: 16px;
	height: 16px;
	fill: #308ccb;
}

.button {
	background: linear-gradient(90deg, rgba(133,192,80,1) 0%, rgba(86,179,198,1) 100%);
	color: #fff;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 21px;
	cursor: pointer;

	&:hover {
		box-shadow: 0px 7px 25px 0px rgba(87, 179, 195, 0.3);
		opacity: .9;
	}
}

.button-empty {
	color: #76ad2b;
	border: 2px solid #76ad2b;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 21px;
	cursor: pointer;
	transition: all 0.2s ease-out;

	&:hover {
		border: none;
		color: #fff;
		background: linear-gradient(90deg, rgba(133,192,80,1) 0%, rgba(86,179,198,1) 100%);
		box-shadow: 0px 7px 25px 0px rgba(87, 179, 195, 0.3);
	}
}

.header-top {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	align-self: center;
	padding: 7px 0;

	a {

		@media (max-width: 720px) {
			width: 100%;
			margin-bottom: 10px;
		}
	}

	img {
		width: 153px;
		height: 61px;
		
		@media (max-width: 720px) {
			width: 100%;
			margin: 5px -10px 10px;
		}
	}

	&__info-wrap {
		display: flex;
		align-self: center;
		flex-wrap: wrap;
		justify-content: center;
		
		@media (max-width: 409px) {
			margin: auto;
			flex-direction: column;
			// align-items: center;
		}
	}

	&__item {
		display: flex;
		align-items: center;
		margin-right: 35px;
		font-weight: 700;
		margin-bottom: 10px;
		margin-top: 10px;
		max-width: 180px;

		@media (max-width: 409px) {
			margin-right: 0;
		}

		svg {
			margin-right: 6px;
		}
	}

	.button {
		height: 42px;
		width: 140px;

		@media (max-width: 720px) {
			margin-top: 10px;
		}
	} 
}

.header-popup {
	background-color: #fff;
	max-width: 740px;
	margin: auto;
	padding: 50px 80px;
	display: flex;
	flex-direction: column;
	align-items: center;
	max-width: 740px;
	position: relative;

	@media (max-width:500px){
		padding: 50px 30px;
	}


	h3 {
		margin-bottom: 22px;
	}

	&__text {
		font-size: 12px;
		color: #989898;
	}

	&__icons-wrap {
		margin-top: 10px;
		margin-bottom: 23px;
		margin-right: -3px;

		svg {
			fill: #8fc841;
			width: 23px;
			height: 23px;
			margin-right: 3px;
		}

		.inactive-cardiology-icon {
			fill: #c4c4c4;
		}
	}

	form {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
	}

	.button {
		border: none;
		max-width: 280px;
		height: 46px;
		margin-top: 20px;
		border-radius: 23px;
		box-shadow: 0px 7px 16px 0px rgba(87, 179, 195, 0.3);

		&:hover {
			box-shadow: 0px 6px 25px 0px rgba(87, 179, 195, 0.4);
		}
	}

	&__input-wrap, input {
		max-width: 280px;
		width: 100%;

		&:nth-child(1) {
			margin-right: 20px;

			@media (max-width:751px) {
				margin-right: 0;
			} 
		}

		@media (max-width:751px) {
			max-width: 580px;
			width: 100%;
		} 
	}

	&__textarea-wrap, textarea {
		max-width: 580px;
		width: 100%;
		min-height: 160px;

	}

	input, textarea {
		margin-bottom: 20px;
		margin-top: 7px;
		border: 1px solid #e4e4e4;
		border-radius: 4px;
		height: 44px;
		padding: 15px;
		outline:  0;
		resize: none;

		&::placeholder {
			color: #d1d1d1;
		}
	}
}

.header-borderline {
	border-bottom: 1px solid #fafbfb;
}

.header-bottom {
	display: flex;
	justify-content: center;
	align-items: center;
	box-shadow: 0 10px 10px 0 rgba(40, 104, 178, 0.02);

	.content {
		padding-top: 18px;
		padding-bottom: 18px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		position: relative;
	}
	

	&__menu {
		display: flex;

		@media (max-width: 1070px) {
			display: none;
		}

		a {
			color: #878888;
			margin-right: 30px;
			flex: 1 1 auto;
		}
	
		a.active {
			color: #2868b2;
		}
	}

	&__sns {
		margin-right: 34px - 12px;
		a {
			margin-right: 12px;
		}
	}
}

.breadcrumbs {
	font-size: 10px;
	color: #c0c0c0;
	height: 20px;
	padding-top: 10px;
	position: relative;
	z-index: 4000;
	&-active {
		cursor: default;
		opacity: 0.8;
	}
	&-solo {
		padding: 0;
		margin: 20px 0;
	}
}

.page-banner {
	background-color: #f8f9fd;
	margin-bottom: 60px;
	min-height: 200px;
	padding-bottom: 20px;

	&__main {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		min-height: 160px;
		padding: 20px 0;

		&-error {
			padding: 50px 0;
		}
	}

	&__header {
		color: #2868b2;
		font-size: 26px;
		margin-bottom: 10px;
		text-align: center;
		max-width: 700px;
	}

	&__subheader {
		font-size: 16px;
		color: #989898;
		text-align: center;
		line-height: 24px;
		max-width: 700px;
	}

	&__404 {
		font-size: 190px;
		line-height: 190px;
		color: #f8f9fd;
		font-weight: 900;
		text-shadow: -7px 4px 6px rgba(50, 85, 225, 0.11), 5px -6px 6px rgba(255, 255, 255, 1);
	}

	&__error {
		font-size: 26px;
		color: #2868b2;
		font-weight: 400;
	}

	.button {
		width: 72px;
		height: 24px;
		font-size: 12px;
		margin-top: 17px;
		box-shadow: 0px 7px 16px 0px rgba(87, 179, 195, 0.3);
		cursor: default;
		
		&:hover {
			opacity: 1;
		}
	}

	&-wom {
		margin-bottom: 0;
	}
}

.burger-header {
	display: none;
	transition: opacity .2s ease-out;
	cursor: pointer;
	
	&:hover {
		opacity: 0.7;
	}

	@media (max-width: 1070px) {
		display: block;
	}
}

.mob-menu {
		display: none;
		background-color: #fff;
		position: fixed;
		z-index: 7000;
		top: 0;
		left: 0;
		min-width: 300px;
		width: 50vw;
		height: 100%;
		padding: 30px 20px 20px;

	&__wrap {
		display: flex;
		flex-direction: column;
	}

	a {
		color: #878888;
		margin-bottom: 25px;
		flex: 1 1 auto;
	}

	.close-button {
		transition: opacity .2s ease-out;
		cursor: pointer;
		align-self: flex-end;
		margin-bottom: 30px;

		svg {
			width: 20px;
			height: 20px;
			fill: #494949;
		}
			
		&:hover {
			opacity: 0.7;
		}
	}

	&__active {
		display: block;
	}

}

.contacts-wrapper {
	display: flex;
	margin-bottom: 80px;
	flex: 1 1 400px;
	justify-content: space-between;

	@media (max-width: 1156px) {
		justify-content: center;
		margin-top: 10px;
		
	}

	@media (max-width: 900px) {
		flex-wrap: wrap;
	}

	.contacts-info {
		display: flex;
		flex-direction: column;
		max-width: 390px;
		width: 100%;


		h3 {
			margin-bottom: 30px;
		}

		&__item {
			display: flex;
			align-items: flex-start;
			margin-bottom: 35px;

			svg {
				width: 20px;
				height: 20px;
				margin-right: 7px;
				margin-top: 4px;
				fill: url(#MyGradient);
			}
		}

		&__text {
			&-head {
				font-size: 12px;
				color: #989898;
				line-height: 1.2rem;
				
			}

			&-desc {
				line-height: 1.2rem;
			}
		}

		.sns-block {
			margin-left: 25px;
			margin-bottom: 30px;

			svg {
				margin-right: 15px;
				width: 20px;
				height: 20px;
			}
		}
	}

	.contacts-form {
		max-width: 667px;
		width: 100%;
		padding: 50px 60px 50px 60px;
		flex: 1 1 670px;
		box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.06);
		border-radius: 4px;

		@media (max-width: 1156px) {
			max-width: 700px;
			padding: 15px
		}


		&__subhead {
			color: #989898;
			margin-top: 7px;
			margin-bottom: 30px;
		}

		form {
			display: flex;
			flex-wrap: wrap;
			min-width: 580px;

			@media (max-width: 800px) {
				flex-direction: column;
				min-width: 0;
			}

			.form-item {
				display: flex;
				flex-direction: column;
				margin-bottom: 18px;
			}

			input[id=form-name] {
				margin-right: 20px;
				min-width: 280px;

				@media (max-width: 800px) {
					min-width: 1px;
					width: 100%;
				}
			}

			input[id=form-phone] {
				min-width: 280px;

				@media (max-width: 800px) {
					min-width: 1px;
					width: 100%;
				}
			}

			input[id=form-message] {
				min-width: 580px;

				@media (max-width: 800px) {
					min-width: 1px;
					width: 100%;
				}
			}

			input::placeholder {
				color: #d7d7d7;
			}

			input {
				border: 1px solid #e4e4e4; 
				border-radius: 4px;
				height: 46px;
				padding-left: 15px;
				max-width: 580px;
				outline: none;
			}

			label {
				color: #989898;
				font-size: 11px;
				margin-bottom: 10px;
			}
			

			.button {
				border-radius: 23px;
				border: none;
				max-width: 280px;
				width: 100%;
				margin-top: 20px;
			}
		}
	}
}

.map {
	position: relative;
		
	#map {
		height: 500px;
		width: 100%;
	 }
}

.footer__text {
	font-weight: 700;
	color: #989898;
	margin-bottom: 11px;

	&-blue {
		color: #3072b9;
	}
}

.footer-top {
	padding: 45px 0;
	background-color: #f8f9fd;

	
	.content {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		flex-wrap: wrap;
		margin-top: -30px;
		margin-bottom: -11px;

		@media (max-width: 1000px) {
			flex-direction: column;
			max-width: 300px;
			padding: 0 10px;
		}
	}

	&__col {
		display: flex;
		flex-direction: column;
		line-height: 1rem;
		margin-right: 20px;
	}

	.footer__text-blue {
		margin-bottom: 15px;
		margin-top: 30px;
	}
}

.footer-main {
	border-top: solid 2px #fff;
	padding: 40px 0;
	background-color: #f8f9fd;

	
	@media (max-width: 1000px) {
	 padding-bottom: 0;
	}

	.content {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	img {
		width: 214px;
		height: 85px;
		margin-right: 20px;

		@media (max-width: 1000px) {
			display: none;
		}
	}

	&__wrap {
		display: flex;
		justify-content: space-between;
		width: 730px;
		flex-wrap: wrap;
		margin-right: -30px;

		@media (max-width: 390px){
			justify-content: center;
		}
	}

	&__col {
		display: flex;
		flex-direction: column;
		margin-bottom: -11px;
		margin-right: 50px;

		@media (max-width: 1000px) {
			margin-bottom: 50px;	
		}
	}
	
	&__sns {
		margin-top: 25px;
		margin-right: -12px;
	

		a {
			margin-right: 12px;
		}

		@media (max-width: 1000px) {
			margin: 25px auto 0;
		}
	}

}

.footer-bottom {
	background-color:#e4e7f4 ;
	
	.content {
		display: flex;
		justify-content: space-between;
		align-items: center;
		flex-wrap: wrap;
		padding: 10px 50px;
		line-height: 0.8rem;

		@media (max-width: 700px) {
			padding: 10px 5px;
			justify-content: center;
		}
	}

	&__item {
		color: #8da5c0;
		font-size: 12px;
		margin: 10px 0;	
		margin-right: 20px;

		&:last-child{
			margin-right: 0;
		}
	}

	.agreement {
		border-bottom: 1px solid #8da5c0;
	}

}

.section-nav {
	background-color: #fff;
	border-radius: 21px; 
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	margin: -21px auto 60px;
	width: fit-content;
	box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.06);
	

	
	@media (max-width: 359px) {
		flex-direction: column;
		width: 100%;
		padding: 15px 30px;
	}

	&__item {
		color: #76ad2b;
		border-radius: 21px;
		display: flex;
		justify-content: center;
		align-items: center;
		min-width: 120px;
		height: 40px;
		cursor: pointer;
		transition: opacity .2s ease-out;

		&-active {
			color: #fff;
			background: linear-gradient(90deg, rgba(133,192,80,1) 0%, rgba(86,179,198,1) 100%);
			cursor: default;
		}

		&-schedule {
			margin-right: 20px;

			@media (max-width: 359px) {
				margin-right: 0;
			}

			&:nth-child(2) {
				color: #76ad2b;
			}
			&:nth-child(3) {
				color: #2868b2;
			}
		}

		&:hover {
			opacity: .7;
		}
	}
}

.section-btn {
	.button {
		width: 180px;
		height: 42px;
		margin: -21px auto 60px;
	}
}

.main-page {
	.subheader-p {
		color: #989898;
		font-size: 16px;
		line-height: 24px;
		text-align: center;
		margin: 10px 0 60px;
	}

	&__preview {
		position: relative;

		img {
			border-radius: 20px;

			@media (max-width: 1000px) {
				opacity: 0.4;
			}
		}

		&-text {
			text-align: left;
			font-size: 16px;
			font-weight: 400;
			line-height: 24px;
			color: #494949;
			max-width: 490px;
			margin-top: 30px;

			@media (max-width: 1000px) {
				text-align: center;
			}
		}

		.button {
			width: 180px;
			height: 42px;
			margin-top: 33px;
			box-shadow: 0px 7px 16px 0px rgba(87, 179, 195, 0.3);
			font-size: 14px;
			font-weight: 500;

			
			@media (max-width: 1000px) {
				margin-right: auto;
				margin-left: auto;
			}
		}

		.swiper-container {
			width: 100%;
			height: 100%;
			z-index: 1;
		}
		 
		.swiper-slide {
			text-align: center;
			font-size: 18px;
			background: #fff;
			position: relative;
			overflow: hidden;
			position: relative;
			display: flex;
			justify-content: center;
			align-items: center;
			
			.content {
				position: absolute;
				h1 {
					text-align: left;

					@media (max-width: 1000px) {
						text-align: center;
					}
				}
			}
		}

		.swiper-button-prev, 
		.swiper-button-next {
			width: 40px;
			height: 40px;
			background-color: #fff;
			border-radius: 100%;
			outline: none;
			box-shadow: 0px 0px 20px 0px rgba(40, 104, 178, 0.02);

			svg {
				height: 10px;
				width: 10px;
				fill: #b4b4b4;
			}

			&:hover {
				opacity: 0.7;
			}
		}

		.swiper-button-prev:after, 
		.swiper-container-rtl .swiper-button-next:after,  
		.swiper-button-next:after, 
		.swiper-container-rtl .swiper-button-prev:after{
			content: none;
			outline: none;
		}

		.swiper-button-prev {
			transform: rotate(180deg);
			left: 40px;

			@media (max-width: 1400px) {
				margin-left: 50%;
				left: -75px;
				top: 550px;
			}
		}

		.swiper-button-next {
			right: 40px;

			@media (max-width: 1400px) {
				margin-right: 50%;
				right: -75px;
				top: 550px;
			}
		}


		&-flexbox {
			display: flex;
			justify-content: center;
			flex-wrap: wrap;
			margin-top: -88px;
			margin-right: -19px;

			@media (max-width: 1400px) {
				margin-top: 60px;
			}
  
			&-item {
				background-color: #fff;
				max-width: 354px;
				width: 100%;
				border-radius: 4px;
				padding: 23px 34px 20px 24px; 
				z-index: 2;
				margin-right: 19px;
				box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.06);
				text-shadow: 0px 0px 20px rgba(0, 0, 0, 0.06);
				margin-bottom: 20px;

				&:nth-child(2) {
					svg {
						fill: #40b1e5;
					}
					.main-page__preview-flexbox-bottom {
						color:  #40b1e5;
					}

					.main-page__preview-flexbox-icon-wrapper {
						background-color: #e4f4fb;
					}
					
				}

				&:hover {
					opacity: 1;
					box-shadow: 0px 16px 40px 0px rgba(0, 0, 0, 0.06);
				}
			}

			&-top {
				display: flex;
				align-items: center;
				font-weight: 500;
				font-size: 16px;
				margin-bottom: 13px;
			}

			&-icon-wrapper {
				border-radius: 100%;
				height: 50px;
				width: 50px; 
				display: flex;
				align-items: center;
				justify-content: center;
				margin-right: 12px;
				&-green {
					background-color: #eff7e4;
					svg {
						fill: #8fc841;
					}
				}
			}

			&-middle {
				color: #a6a6a6;
				line-height: 18px;
				margin-bottom: 21px;
			}

			&-bottom {
				display: flex;
				align-items: center;
				&-green {
					color: #8fc841;
					svg {
						fill: #8fc841;
						margin-left: 8px;
						height: 10px;
						width: 10px;
					}
				}
			}
		}
	}

	&__personal {
		padding-top: 100px;
		padding-bottom: 80px;

		h2, h3 {
			font-weight: 500;
			text-align: left;
			line-height: 40px ;
		}

		.content {
