.schedule {
	padding-bottom: 60px;

	&__flexbox {
		display: flex;
		align-items: flex-start;
		margin-right: -10px;
		margin-bottom: -30px;

		@media (max-width: 1150px) {
			flex-wrap: wrap;
			justify-content: center;
		}
	}

	&__block {
		display: flex;
		flex-wrap: wrap;
		width: 155px;
		flex: 1 1 fit-content;
		margin-right: 10px;
		margin-bottom: 30px;

			
		@media (max-width: 1150px) {
			width: 200px;
		}

		&:nth-child(4) {
			.schedule__card:nth-child(2n+1) {
				.colored-bot { 
					background-color: #76ad2b;
				}
				&:hover {
					background-color: #76ad2b;
				}
			
			}
			.schedule__card:nth-child(2n) {
				.colored-bot { 
					background-color: #2868b2;
				}				
				&:hover {
					background-color: #2868b2;
				}
			}
		}

		&:nth-child(5) {
			color: #fff;
			.schedule__card-header, .schedule__card-grey {
				color: #fff;
			}
			.schedule__card:nth-child(2) {
				background-color: #76ad2b;
			}
			.schedule__card:not(:nth-child(2)) {
				background-color: #2868b2;
			}
		}
	}

	&__day {
		height: 55px;
		font-size: 16px;
		color: #989898;
		padding: 9px 15px;
	}

	&__card {
		padding: 9px 15px;
		box-shadow: 0px 0px 10px 0px rgba(23, 14, 17, 0.04);
		margin-bottom: 10px;
		height: 85px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		width: 148px;
		border-radius: 4px;
		position: relative;
		transition: background-color .4s ease-out;

		
		@media (max-width: 1150px) {
			width: 200px;
		}
	
		
		&-header {
			color:#2868b2;
			line-height: 16px;
			margin-bottom: 5px;
		}

		&-grey {
			color: #aaaaaa;
			font-size: 12px;
			font-weight: 500;
		}

		&-footer {
			display: flex;
			justify-content: space-between;
			line-height: 15px;
			font-weight: 500;
		}
	}

	&__card:hover {
		background-color: #76ad2b;
		.schedule__card-header,
		.schedule__card-grey,
		.schedule__card-footer {
			color: #fff;
		}
	}

	.colored-bot {
		min-height: 3px;
		width: 100%;
		position: absolute;
		bottom: 0;
		left: 0;
		border-radius: 0 0 4px 4px;
	}
}



